
import XHeader from 'vux/src/components/x-header/index.vue'

import { getTradelist } from 'api'
export default {
data () {
return {
info: null,
blk: this.$route.params.args
}
},
created () {
this.getTradelist()
},
computed: {
fee () {
let total
if (this.blk.length > 45) {
total = this.info.fee
} else {
let {black, nMinerFee, sMinerFee} = this.info
total = Number(black) + Number(nMinerFee) + Number(sMinerFee)
}
return total
},
status () {
let status = ''
switch (this.info.status) {
case '0':
status = '处理中'
break
case '1':
status = '交易成功'
break
case '2':
status = '交易失败'
break
default:
status = '处理中'
break
}
return status
}
},
methods: {
getTradelist () {
this.$vux.loading.show()
let args = {}
if (this.blk.length > 45) {
args = {
method: 'txrst',
tx: this.blk
}
} else {
args = {
method: 'qryTxByBlk',
blk: this.blk
}
}
getTradelist(args).then(res => {
let info = Array.isArray(res.data) ? res.data : [res.data]
if (this.blk.length > 45) {
info.map(item => {
item.facc = item.out
item.tacc = item.in
})
}
this.info = info[0]
})
.finally(() => {
this.$vux.loading.hide()
})
},
getRecord (acc) {
this.$router.push({name: 'BlockRecord', query: {acc}})
}
},
components: {
XHeader
}
}



